export default function (editor, opt = {}) {
    const c = opt;
    

    const domc = editor.DomComponents;
    const defaultType = domc.getType('default');
    const defaultModel = defaultType.model;
    const defaultView = defaultType.view;

    const traits = [
        {
            name: 'altFormat',
            placeholder: 'September 23, 2020'
        },
        {
            name: 'defalutDate',
            value: '2020/08/08'
        },
        {
            name: 'maxDate'
        },
        {
            name: 'minDate',
            changeProps: 1,
        },
        {
            type: 'select',
            name: 'mode',
            options: [
                { id: 'single', name: 'Single' },
                { id: 'multiple', name: 'Multiple' },
                { id: 'range', name: 'Range' }
            ],
            value: 'single',
            changeProps: 1
        },
        {
            type: 'checkbox',
            name: 'enableTime',
            valueTrue: 'true',
            valueFalse: 'false',
            changeProps: 1
        },
        {
            type: 'checkbox',
            name: 'enableSeconds',
            valueTrue: 'true',
            valueFalse: 'false',
            changeProps: 1
        }
    ];
    domc.addType(c.inputType, {
        model: defaultModel.extend({
            defaults: {
                ...defaultModel.prototype.defaults,
                name: c.labelName,
                scriptsrc: c.script,
                stylesrc: c.style,
                traits,
                script: function () {
                    const fpConfig = {
                        altFormat: this.getAttribute('altformat'),
                        maxDate: this.getAttribute('maxdate'),
                        minDate: this.getAttribute('mindate'),
                        defaultDate: this.getAttribute('defalutdate'),
                        mode: this.getAttribute('mode'),
                        enableTime: this.getAttribute('enabletime'),
                        enableSeconds: this.getAttribute('enableseconds')
                    }
                    console.log(fpConfig)
                    const init = () => {
                        const el = this;
                        el.innerHTML = `<input class="flatpickr flatpickr-input input" data-gjs-type="input" style="width:100%;" />`
                        const fp = flatpickr('.flatpickr-input', fpConfig);
                    };
                    const appendStyle = () => {
                        const link = document.createElement('link');
                        link.href = '{[ stylesrc ]}';
                        link.rel = 'stylesheet';
                        document.head.appendChild(link);
                    };
                    const appendScript = () => {
                        const scr = document.createElement('script');
                        scr.src = '{[ scriptsrc ]}';
                        scr.onload = init;
                        document.head.appendChild(scr);
                    }
                    if (!window.flatpickr) {
                        appendStyle();
                        appendScript();
                    } else {
                        init();
                    }
                }
            },
            init() {
                this.on('change:attributes', () => this.trigger('change:script'));
            }
        }, {
            isComponent(el) {
                if (el.getAttribute &&
                    el.getAttribute('data-gjs-type') == c.inputType) {
                    return {
                        type: c.inputType
                    };
                }
            },
        }),
        view: defaultView.extend({
            init() {
                const comps = this.model.get('components');
                if (!comps.length) {
                    comps.reset();
                }
            }
        })
    });
}