export default function (editor, opt = {}) {
    const c = opt;
    let bm = editor.BlockManager;
    bm.add(c.inputType, {
        label: c.labelName,
        category: c.category,
        content: `
        <div class="${c.labelPfx}-container" data-gjs-type="${c.inputType}"></div>`
    });
}